package io.mapshot.sharedCommon.common.orientation

import io.mapshot.sharedCommon.common.KmmMath
import io.mapshot.sharedCommon.common.jsonReader.FieldOfViewBorders
import io.mapshot.sharedCommon.common.jsonReader.LatLng
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmSizeF
import kotlin.math.atan
import kotlin.math.atan2

class AngleCalculationsForImg {
    fun getHorizontalAngleDiffToImageCenter(
        bitmapWidth: Int,
        scalingFactorX: Float,
        focalLength: Double,
        pixelXOfInterest: Int
    ): Double {
        val pointXOnSensor = (pixelXOfInterest - bitmapWidth / 2) / scalingFactorX
        return KmmMath().toDegrees(atan2(pointXOnSensor.toDouble(), focalLength))
    }

    fun getVerticalAngleDiffToImageCenter(
        bitmapHeight: Int,
        scalingFactorY: Float,
        focalLength: Double,
        pixelYOfInterest: Int
    ): Double {
        val pointYOnSensor = -(pixelYOfInterest - bitmapHeight / 2) / scalingFactorY
        return KmmMath().toDegrees(atan2(pointYOnSensor.toDouble(), focalLength))
    }

    fun getFov(sensorSize: KmmSizeF, focalLength: Double): Double {
        return KmmMath().toDegrees(2 * atan(sensorSize.width / (2 * focalLength)))
    }


    // todo: this does not return values in 360 range. As I do not have the overview over all usages of the result of that function,
    //  I do not want to change this to return only values in the 360 range. I already tried this and found out, that
    //  the elevationData download misbehaves. Fix this as soon as unit tests cover the code stability
    fun getAzimuthImgLeftRightBorderATTENTIONThisIsNotIn360Range(
        bitmapWidth: Int,
        scalingFactorX: Float,
        focalLength: Double,
        sensorKmmSizeF: KmmSizeF,
        imgCreatorGeoPoint: LatLng,
        landmarkPxlX: Int,
        landmarkLatLng: LatLng
    ): FieldOfViewBorders {
        val azimuthDiffToCenter = AngleCalculationsForImg().getHorizontalAngleDiffToImageCenter(
            bitmapWidth,
            scalingFactorX,
            focalLength,
            landmarkPxlX
        )
        val azimuthImgCenter = GeoCalculations().getAzimuth(
            imgCreatorGeoPoint,
            landmarkLatLng
        )
        val fov = AngleCalculationsForImg().getFov(sensorKmmSizeF, focalLength)
        val azimuthImgLeftBorder = azimuthImgCenter - fov / 2 - azimuthDiffToCenter // todo: angles are added (does this affect 360 range of angles?)
        val azimuthImgRightBorder = azimuthImgCenter + fov / 2 - azimuthDiffToCenter// todo: angles are added (does this affect 360 range of angles?)
        return FieldOfViewBorders(azimuthImgLeftBorder, azimuthImgRightBorder)
    }
}