package io.mapshot.sharedCommon.common.logging

import co.touchlab.kermit.Logger

class Logging {
    fun logDebug(debugNumber: DebugLogNumber?, message: String) {
        Logger.d("$debugNumber") { message }
    }

    fun logError(errorNumber: ErrorLogNumber, message: String) {
        Logger.e("$errorNumber") { message }
    }
}