package io.mapshot.sharedCommon.common.folderHandling._nativeCode

import androidx.compose.runtime.Composable
import androidx.compose.ui.graphics.ImageBitmap
import io.mapshot.sharedCommon.common.folderHandling.FileNames
import io.mapshot.sharedCommon.common.folderHandling.kmmFile.KmmFile
import mapshot.sharedcommon.generated.resources.Res
import mapshot.sharedcommon.generated.resources.mapshotViewerExample_img
import org.jetbrains.compose.resources.imageResource

@Composable
actual fun getImageBitmap(imageFile: KmmFile): ImageBitmap? {
    return when (imageFile.file) {
        FileNames.IMAGE_FILE -> {
            imageResource(Res.drawable.mapshotViewerExample_img)
        }
        else -> {
            null
        }
    }
}