package io.mapshot.sharedCommon.common.orientation

import io.mapshot.sharedCommon.common.KmmMath
import io.mapshot.sharedCommon.common.jsonReader.CreatorPosition
import io.mapshot.sharedCommon.common.jsonReader.LatLng
import io.mapshot.sharedCommon.common.jsonReader.MyGeoPoint
import io.mapshot.sharedCommon.common.jsonReader.asLatLng
import kotlin.math.asin
import kotlin.math.atan2
import kotlin.math.cos
import kotlin.math.sin
import kotlin.math.sqrt

class GeoCalculations {
    fun getAzimuth(location1: LatLng, location2: LatLng): Double {
        val myLatRad = KmmMath().toRadians(location1.latitude)
        val myLngRad = KmmMath().toRadians(location1.longitude)
        val desiredLatRad = KmmMath().toRadians(location2.latitude)
        val desiredLngRad = KmmMath().toRadians(location2.longitude)

        val deltaLongitude = desiredLngRad - myLngRad

        val y = sin(deltaLongitude) * cos(desiredLatRad)
        val x = cos(myLatRad) * sin(desiredLatRad) - sin(myLatRad) * cos(desiredLatRad) * cos(deltaLongitude)

        val azimuth = KmmMath().toDegrees(atan2(y, x))

        return (azimuth + 360) % 360
    }

    // Example for 0 vertical angle (X is the image creator, the rest is mountain and an arrow that shows the vertical view angle)
    //                                   ___________
    // ___X-------------------------->/
    //     \     _                   /
    //      \___/ \                 /
    //             \_______________/
    //
    fun getVerticalAngleFromCreatorToRealPointInWorld(imgCreatorPosition: CreatorPosition, desiredGeoPoint: MyGeoPoint): Double {
        val distance = calculateDistanceHaversine(
            imgCreatorPosition.asLatLng(),
            desiredGeoPoint.asLatLng()
        )
        val elevationWithEarthCurvatureIncluded = getElevationWithEarthCurvatureIncluded(desiredGeoPoint.elevation, distance)
        val deltaElevation = elevationWithEarthCurvatureIncluded - (imgCreatorPosition.geoPoint.elevation + imgCreatorPosition.metresAboveGround)
        return KmmMath().toDegrees(atan2(deltaElevation, distance))
    }

    fun calculateDistanceHaversine(location1: LatLng, location2: LatLng): Double {
        val earthRadius = 6371000 // radius of Earth in meters
        val dLat = KmmMath().toRadians(location2.latitude - location1.latitude)
        val dLon = KmmMath().toRadians(location2.longitude - location1.longitude)

        val a = sin(dLat / 2) * sin(dLat / 2) +
                cos(KmmMath().toRadians(location1.latitude)) * cos(KmmMath().toRadians(location2.latitude)) *
                sin(dLon / 2) * sin(dLon / 2)

        val c = 2 * atan2(sqrt(a), sqrt(1 - a))

        return earthRadius * c
    }


    private fun getElevationWithEarthCurvatureIncluded(elevation: Double, distance: Double): Double {
        val earthRadius = 6371000.0
        val earthCurvaturePart = sqrt(distance * distance + earthRadius * earthRadius) - earthRadius

        return elevation - earthCurvaturePart
    }

    fun coordinatesOfAPointAlongView(initialLocation: LatLng, distanceInMetres: Double, orientationAngle: Double): LatLng {
        val radius = 6371000.0 // radius of the earth in m
        val lat1 = KmmMath().toRadians(initialLocation.latitude)
        val lng1 = KmmMath().toRadians(initialLocation.longitude)
        val bearing = KmmMath().toRadians(orientationAngle)
        val lat2 = asin(sin(lat1) * cos(distanceInMetres / radius) + cos(lat1) * sin(distanceInMetres / radius) * cos(bearing))
        val lng2 = lng1 + atan2(sin(bearing) * sin(distanceInMetres / radius) * cos(lat1), cos(distanceInMetres / radius) - sin(lat1) * sin(lat2))

        return LatLng(KmmMath().toDegrees(lat2), KmmMath().toDegrees(lng2))
    }
}