package io.mapshot.sharedCommon.mapshotViewer.map

import androidx.compose.runtime.Composable
import androidx.lifecycle.viewmodel.compose.viewModel
import io.mapshot.sharedCommon.common.map._nativeCode.MapController
import io.mapshot.sharedCommon.common.map._nativeCode.MapControllerViewModel
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.MapToMaskConversionViewModel

@Composable
fun MapshotViewerMap(mapToMaskConversionViewModel: MapToMaskConversionViewModel) {
    val mapshotViewerMapControllerViewModel = viewModel { MapshotViewerMapControllerViewModel() }
    val platformMapController = MapshotViewerMapController(mapshotViewerMapControllerViewModel)
    platformMapController.MapComposable(
        onTap = { latLngOfTappedPoint -> mapshotViewerMapControllerViewModel.addSingleMarker(latLngOfTappedPoint) },
        MaskComposable = {
            platformMapController.SingleMarkerMask()
        }
    )
}