package io.mapshot.sharedCommon.mapshotViewer.image

import androidx.compose.foundation.layout.BoxWithConstraints
import androidx.compose.foundation.layout.aspectRatio
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.ImageBitmap
import androidx.lifecycle.viewmodel.compose.viewModel
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmSize
import io.mapshot.sharedCommon.mapshotViewer.image.mask.Mask
import io.mapshot.sharedCommon.mapshotViewer.image.utils.PixelCanvasConverter
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.MapToMaskConversionViewModel


@Composable
fun MapshotViewerImage(
    originalImageBitmap: ImageBitmap?,
    mapToMaskConversionViewModel: MapToMaskConversionViewModel
) {
    originalImageBitmap?.let { thisImageBitmap ->
        val aspectRatio = thisImageBitmap.width.toFloat() / thisImageBitmap.height.toFloat()

        BoxWithConstraints(
            modifier = Modifier
                .fillMaxSize()
                .aspectRatio(aspectRatio)
        ) {
            val pixelCanvasConverter = PixelCanvasConverter(constraints, KmmSize(thisImageBitmap.width, thisImageBitmap.height))

            val zoomStateViewModel: ZoomStateViewModel = viewModel { ZoomStateViewModel() }
            ZoomableImage(thisImageBitmap, constraints, zoomStateViewModel)
            Mask(pixelCanvasConverter, zoomStateViewModel, mapToMaskConversionViewModel, thisImageBitmap.width)

        }
    }
}