package io.mapshot.sharedCommon.mapshotViewer.image.mask

import androidx.compose.foundation.Canvas
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.runtime.State
import androidx.compose.runtime.collectAsState
import androidx.compose.ui.Modifier
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.util.fastForEachReversed
import io.mapshot.sharedCommon.common.api.overpassApi.PoiType
import io.mapshot.sharedCommon.common.jsonReader.PixelPosition
import io.mapshot.sharedCommon.mapshotViewer.image.ZoomStateViewModel
import io.mapshot.sharedCommon.mapshotViewer.image.utils.PixelCanvasConverter
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.MapToMaskConversionViewModel


@Composable
fun Mask(
    pixelCanvasConverter: PixelCanvasConverter,
    zoomStateViewModel: ZoomStateViewModel,
    mapToMaskConversionViewModel: MapToMaskConversionViewModel,
    imageWidth: Int
) {
    val scale = zoomStateViewModel.scale.collectAsState()
    val translation = zoomStateViewModel.translation.collectAsState()
    MapToMaskHorizonLine(mapToMaskConversionViewModel, pixelCanvasConverter, scale, translation, imageWidth)
    MapToMaskMarker(mapToMaskConversionViewModel, pixelCanvasConverter, scale, translation)
    MapToMaskPoiPoints(mapToMaskConversionViewModel, pixelCanvasConverter, scale, translation)
}

@Composable
private fun MapToMaskPoiPoints(
    mapToMaskConversionViewModel: MapToMaskConversionViewModel,
    pixelCanvasConverter: PixelCanvasConverter,
    scale: State<Float>,
    translation: State<Offset>
) {
    val mapToMaskSettlements = mapToMaskConversionViewModel.mapToMaskPoiPoints.collectAsState()
    val poiPointsToShow = mapToMaskSettlements.value.take(10)
    poiPointsToShow.fastForEachReversed { poiPoint ->
        poiPoint.imagePixel?.directImagePoint?.let { directImagePoint ->
            when (poiPoint.poiType) {
                PoiType.SETTLEMENT -> {
                    PoiLabel(poiPoint, pixelCanvasConverter, scale, translation, directImagePoint, Color.White, Color.Black)
                }

                PoiType.MOUNTAIN -> {
                    PoiLabel(poiPoint, pixelCanvasConverter, scale, translation, directImagePoint, MaterialTheme.colors.primary, MaterialTheme.colors.onPrimary)
                }
            }
        }
    }
}

@Composable
private fun MapToMaskMarker(
    mapToMaskConversionViewModel: MapToMaskConversionViewModel,
    pixelCanvasConverter: PixelCanvasConverter,
    scale: State<Float>,
    translation: State<Offset>
) {
    val mapToMaskMarker = mapToMaskConversionViewModel.mapToMaskMarker.collectAsState()
    mapToMaskMarker.value?.imagePixel?.directImagePoint?.let {
        Canvas(modifier = Modifier.fillMaxSize()) {
            drawCircle(
                color = Color.Red,
                radius = 10f,
                center = pixelCanvasConverter.pixelPointToCanvas(scale.value, translation.value, PixelPosition(it.x.toInt(), it.y.toInt()))
            )
        }
    }
}

@Composable
private fun MapToMaskHorizonLine(
    mapToMaskConversionViewModel: MapToMaskConversionViewModel,
    pixelCanvasConverter: PixelCanvasConverter,
    scale: State<Float>,
    translation: State<Offset>,
    imageWidth: Int
) {
    val mapToMaskHorizonLine = mapToMaskConversionViewModel.mapToMaskHorizonLine.collectAsState()
    val startPoint = Offset(0f, mapToMaskHorizonLine.value.firstOrNull()?.directImagePoint?.y ?: 0f)
    var previousPoint = startPoint
    Canvas(modifier = Modifier.fillMaxSize()) {
        mapToMaskHorizonLine.value.forEach {
            it?.directImagePoint?.let { directImagePoint ->
                drawLine(
                    color = Color.Black,
                    start = pixelCanvasConverter.pixelPointToCanvas(
                        scale.value,
                        translation.value,
                        PixelPosition(previousPoint.x.toInt(), previousPoint.y.toInt())
                    ),
                    end = pixelCanvasConverter.pixelPointToCanvas(
                        scale.value,
                        translation.value,
                        PixelPosition(directImagePoint.x.toInt(), directImagePoint.y.toInt())
                    ),
                    strokeWidth = 5f
                )
                previousPoint = Offset(directImagePoint.x, directImagePoint.y)
            }
        }

        // draw a line to the end of the image
        val lineEnd = Offset(imageWidth.toFloat(), mapToMaskHorizonLine.value.lastOrNull()?.directImagePoint?.y ?: 0f)
        drawLine(
            color = Color.Black,
            start = pixelCanvasConverter.pixelPointToCanvas(scale.value, translation.value, PixelPosition(previousPoint.x.toInt(), previousPoint.y.toInt())),
            end = pixelCanvasConverter.pixelPointToCanvas(scale.value, translation.value, PixelPosition(lineEnd.x.toInt(), lineEnd.y.toInt())),
            strokeWidth = 5f
        )
        previousPoint = startPoint
    }
}