package io.mapshot.sharedCommon.mapshotViewer.image

import androidx.compose.foundation.Image
import androidx.compose.foundation.gestures.detectTransformGestures
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.runtime.Composable
import androidx.compose.runtime.collectAsState
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.ImageBitmap
import androidx.compose.ui.graphics.graphicsLayer
import androidx.compose.ui.input.pointer.pointerInput
import androidx.compose.ui.unit.Constraints


@Composable
fun ZoomableImage(
    thisImageBitmap: ImageBitmap,
    constraints: Constraints,
    zoomStateViewModel: ZoomStateViewModel
) {

    val scale = zoomStateViewModel.scale.collectAsState()
    val translation = zoomStateViewModel.translation.collectAsState()

    Image(
        bitmap = thisImageBitmap,
        contentDescription = "Mapshot",
        modifier = Modifier
            .fillMaxSize()
            .pointerInput(Unit) {
                detectTransformGestures { _, pan, zoom, _ ->
                    zoomStateViewModel.updateZoomState(zoom, pan, constraints)
                }
            }
            .graphicsLayer(
                scaleX = scale.value,
                scaleY = scale.value,
                translationX = translation.value.x,
                translationY = translation.value.y
            )
    )
}