package io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.utils

import io.mapshot.sharedCommon.common.jsonReader.CreatorPosition
import io.mapshot.sharedCommon.common.jsonReader.ElevationDataFileContent
import io.mapshot.sharedCommon.common.jsonReader.LatLng
import io.mapshot.sharedCommon.common.jsonReader.MyGeoPoint
import io.mapshot.sharedCommon.common.jsonReader.MyLatLng
import io.mapshot.sharedCommon.common.jsonReader.asLatLng
import io.mapshot.sharedCommon.common.logging.DebugLogNumber
import io.mapshot.sharedCommon.common.logging.Logging
import io.mapshot.sharedCommon.common.orientation.AngleCalculations
import io.mapshot.sharedCommon.common.orientation.GeoCalculations
import kotlin.math.ceil
import kotlin.math.floor

data class PointVisibility(
    val distance: Double,
    val verticalAngle: Double,
    val visible: Boolean,
    val elevationGeoPoint: MyGeoPoint
)

data class StreamVisibility(
    var azimuth: Double,
    val streamVisibility: List<PointVisibility>
)

data class HorizonLinePoint(
    val azimuth: Double,
    val pointVisibility: PointVisibility?
)

class VisiblePointsCalculator(
    private var imgCreatorPosition: CreatorPosition,
    elevationDataFileContent: ElevationDataFileContent
) {
    private var allStreamsVisibility: List<StreamVisibility>
    private var horizonLine: List<HorizonLinePoint>
    private var transitionPointsVisibleToNonVisible: MutableList<PointVisibility>
    private var allTransitionPoints: MutableList<StreamVisibility>

    init {
        allStreamsVisibility = calculateVisibilityOfStreams(elevationDataFileContent, imgCreatorPosition)
        horizonLine = calculateHorizonLine()
        transitionPointsVisibleToNonVisible = calculateTransitionPointsVisibleToNonVisible()
        allTransitionPoints = calculateAllTransitionPoints()
    }

    fun getStreamsVisibility(): List<StreamVisibility> {
        return allStreamsVisibility
    }

    fun getHorizonLine(): List<HorizonLinePoint> {
        return horizonLine
    }

    fun getHorizonLineAsMyLatLngData(): List<MyLatLng>? {
        return horizonLine.map {
            val latitude = it.pointVisibility?.elevationGeoPoint?.lat
            val longitude = it.pointVisibility?.elevationGeoPoint?.lng
            if (latitude != null && longitude != null) {
                MyLatLng(latitude, longitude)
            } else {
                return null
            }
        }
    }

    fun getTransitionPointsVisibleToNonVisible(): MutableList<PointVisibility> {
        return transitionPointsVisibleToNonVisible
    }

    fun getAllTransitionPoints(): MutableList<StreamVisibility> {
        return allTransitionPoints
    }

    private fun calculateHorizonLine(): List<HorizonLinePoint> {
        val highestVisiblePoints: List<HorizonLinePoint> = allStreamsVisibility.map { streamVisibility ->
            // Find the PointsVisibility with the largest vertical angle that is visible
            val highestPointVisibility = streamVisibility.streamVisibility
                .filter { it.visible }
                .maxByOrNull { it.verticalAngle }

            HorizonLinePoint(azimuth = streamVisibility.azimuth, pointVisibility = highestPointVisibility)
        }
        return highestVisiblePoints
    }

    private fun calculateTransitionPointsVisibleToNonVisible(): MutableList<PointVisibility> {
        val maxDistance = 300000.0
        val transitionPoints = mutableListOf<PointVisibility>()
        allStreamsVisibility.forEach {
            for (i in 0 until it.streamVisibility.size - 1) {
                val point1 = it.streamVisibility[i]
                val point2 = it.streamVisibility[i + 1]
                val distance = point1.distance
                if (point1.visible && !point2.visible && distance > 0.0 && distance < maxDistance) {
                    transitionPoints.add(point1)
                }
            }
        }
        return transitionPoints
    }

    private fun calculateAllTransitionPoints(): MutableList<StreamVisibility> {
        val transitionPointsMap = mutableListOf<StreamVisibility>()
        allStreamsVisibility.forEach {
            val transitionPoints = mutableListOf<PointVisibility>()
            for (i in 0 until it.streamVisibility.size - 1) {
                val point1 = it.streamVisibility[i]
                val point2 = it.streamVisibility[i + 1]
                val distance = point1.distance
                if (distance > 0.0) {
                    if (point1.visible && !point2.visible ||
                        !point1.visible && point2.visible
                    ) {
                        transitionPoints.add(point1)
                    }
                }
            }
            transitionPointsMap.add(StreamVisibility(it.azimuth, transitionPoints))
        }
        return transitionPointsMap

    }


    private fun calculateVisibilityOfStreams(elevationDataFileContent: ElevationDataFileContent, imgCreatorPosition: CreatorPosition): List<StreamVisibility> {

        val allStreamsVisibility = mutableListOf<StreamVisibility>()
        for (elevationDataStream in elevationDataFileContent.elevationDataStreams) {
            val visibilityOfSingleStream = calculateVisibilityOfSingleStream(
                elevationDataStream.geoPoints,
                elevationDataFileContent.stepSizeDistance,
                imgCreatorPosition
            )
            allStreamsVisibility.add(
                StreamVisibility(elevationDataStream.azimuth, visibilityOfSingleStream)
            )
        }
        return allStreamsVisibility
    }

    // todo: There may be a bug in this function. When I took a photo in Colmar in direction to the castle in the mountains, the visible area points on the map were obviously wrong
    private fun calculateVisibilityOfSingleStream(
        streamGeoPoints: List<MyGeoPoint>,
        stepSizeDistance: Double,
        imgCreatorPosition: CreatorPosition
    ): MutableList<PointVisibility> {
        val verticalAngles = mutableListOf<Double>()
        for (streamGeoPoint in streamGeoPoints) {
            val verticalAngle = GeoCalculations().getVerticalAngleFromCreatorToRealPointInWorld(imgCreatorPosition, streamGeoPoint)
            verticalAngles.add(verticalAngle)
        }

        val visiblePoints = mutableListOf<PointVisibility>()

        var verticalAngleMax = -90.0
        for (i in streamGeoPoints.indices) {
            val streamPoint = streamGeoPoints[i]
            val verticalAngle = verticalAngles[i]
            val distance = (i + 1) * stepSizeDistance

            // sometimes elevation data is imprecise and place a small imaginary hill in front of the camera
            // so we need to make the first points visible by default to avoid this (the first point are mostly visibly anyways, because the normal user takes
            // photos of a scenery in front of him)
            if (distance < 500) {
                visiblePoints.add(PointVisibility(distance, verticalAngle, true, streamPoint))
                continue
            }

            val visible: Boolean
            if (verticalAngle > verticalAngleMax) {
                verticalAngleMax = verticalAngle
                visible = true
            } else {
                visible = false
            }
            visiblePoints.add(PointVisibility(distance, verticalAngle, visible, streamPoint))
        }

        return visiblePoints
    }

    fun getStreamVisibilityOfAzimuthNeighbours(desiredPointAzimuth: Double): List<StreamVisibility>? {
        val azimuthNeighbourLeft = floor(desiredPointAzimuth).toInt()
        val azimuthNeighbourRight = ceil(desiredPointAzimuth).toInt()

        // filter out the azimuths that are the neighbours with azimuth == azimuthNeighbourLeft or azimuthNeighbourRight
        val azimuthNeighbourLeftPoints = allStreamsVisibility.firstOrNull { it.azimuth == azimuthNeighbourLeft.toDouble() }
        val azimuthNeighbourRightPoints = allStreamsVisibility.firstOrNull { it.azimuth == azimuthNeighbourRight.toDouble() }

        val azimuthNeighbours = mutableListOf<StreamVisibility>()
        return if (azimuthNeighbourLeftPoints != null && azimuthNeighbourRightPoints != null) {
            azimuthNeighbours.add(azimuthNeighbourLeftPoints)
            azimuthNeighbours.add(azimuthNeighbourRightPoints)
            azimuthNeighbours
        } else {
            Logging().logDebug(DebugLogNumber.DEBUG_LOG_73, "No azimuth neighbours found $azimuthNeighbourLeft $azimuthNeighbourRight")
            null
        }
    }

    fun getInterpolatedGeoPointFromScatter(desiredPointToMapOnMask: LatLng, imgCreatorPosition: CreatorPosition): MyGeoPoint? {
        val desiredPointAzimuth = GeoCalculations().getAzimuth(imgCreatorPosition.geoPoint.asLatLng(), desiredPointToMapOnMask)
        val azimuthNeighbours = getStreamVisibilityOfAzimuthNeighbours(desiredPointAzimuth)
        getElevationOfNextNeighboursPoint(desiredPointToMapOnMask, azimuthNeighbours)?.let {
            return MyGeoPoint(
                desiredPointToMapOnMask.latitude,
                desiredPointToMapOnMask.longitude,
                it
            )
        } ?: return null
    }

    private fun getElevationOfNextNeighboursPoint(desiredPointToMapOnMask: LatLng, azimuthNeighbours: List<StreamVisibility>?): Double? {
        if (azimuthNeighbours?.size != 2) {
            Logging().logDebug(DebugLogNumber.DEBUG_LOG_103, "azimuthNeighbours.size != 2")
            return null
        }
        val desiredPointAzimuth = GeoCalculations().getAzimuth(imgCreatorPosition.geoPoint.asLatLng(), desiredPointToMapOnMask)
        val desiredPointDistance = GeoCalculations().calculateDistanceHaversine(imgCreatorPosition.geoPoint.asLatLng(), desiredPointToMapOnMask)
        val streamVisibilityOfAzimuthNeighbourLeft = azimuthNeighbours[0]
        val streamVisibilityOfAzimuthNeighbourRight = azimuthNeighbours[1]
        val closestStream = if (
            AngleCalculations().calculateAngleDiffFor180Range(desiredPointAzimuth, streamVisibilityOfAzimuthNeighbourLeft.azimuth) >
            AngleCalculations().calculateAngleDiffFor180Range(desiredPointAzimuth, streamVisibilityOfAzimuthNeighbourRight.azimuth)
        ) {
            streamVisibilityOfAzimuthNeighbourRight
        } else {
            streamVisibilityOfAzimuthNeighbourLeft
        }
        val closestStreamIndex = closestStream.streamVisibility.indexOfFirst { it.distance >= desiredPointDistance }
        when (closestStreamIndex) {
            -1 -> {
                return closestStream.streamVisibility.last().elevationGeoPoint.elevation
            }

            closestStream.streamVisibility.size - 1 -> {
                return closestStream.streamVisibility[closestStreamIndex].elevationGeoPoint.elevation
            }

            else -> {
                val interpolatedElevation = getInterpolatedAlongDistance(
                    closestStream.streamVisibility[closestStreamIndex],
                    closestStream.streamVisibility[closestStreamIndex + 1],
                    desiredPointDistance
                )
                return interpolatedElevation
            }
        }
    }

    private fun getInterpolatedAlongDistance(pointClose: PointVisibility, pointFar: PointVisibility, desiredPointDistance: Double): Double? {
        val distancePointsLeft = pointFar.distance - pointClose.distance
        val distancePointLeftToDesired = desiredPointDistance - pointClose.distance
        if (distancePointLeftToDesired > distancePointsLeft) {
            Logging().logDebug(DebugLogNumber.DEBUG_LOG_104, "distancePointLeftToDesired > distancePointsLeft")
            return null
        }
        val distancePointLeftToDesiredPercentage = distancePointLeftToDesired / distancePointsLeft
        val elevationLeft = pointClose.elevationGeoPoint.elevation
        val elevationLeftNext = pointFar.elevationGeoPoint.elevation
        val interpolatedElevationLeft = elevationLeft + (elevationLeftNext - elevationLeft) * distancePointLeftToDesiredPercentage
        return interpolatedElevationLeft
    }
}