package io.mapshot.sharedCommon.common.jsonReader

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable

data class LatLng(val latitude: Double, val longitude: Double) {
    var time: Long? = null
}

fun MyGeoPoint.asLatLng(): LatLng {
    return LatLng(lat, lng)
}

fun MyLatLng.asLatLng(): LatLng {
    return LatLng(lat, lng)
}

fun CreatorPosition.asLatLng(): LatLng {
    return LatLng(geoPoint.lat, geoPoint.lng)
}

@Serializable
data class InfoFileData(
    var creator: String? = null,
    val version: String,
    val timestamp: Long,
    @SerialName("pitchOnPhotoCapture") var compassYawValue: Float,
    val imgCreatorDeviceInfo: DeviceInfo,
    val imgCreatorPosition: CreatorPosition,
    val landmark: Landmark,
    var fov: FieldOfViewBorders? = null,
    var imageRotation: Double = 0.0,
    @SerialName("factorFromVerticalAdjustmentByUser") var landmarkVerticalOffsetToFitHorizonLine: Double = 0.0,
    @SerialName("factorFromHorizontalAdjustmentByUser") var stretchingFactorDueToSecondLandmark: Float = 1.0f,
    var horizonLineGeoPoints: List<MyLatLng>? = null
)

@Serializable
data class DeviceInfo(
    val phoneType: String,
    val creatorAddress: String,
    val cameraParameters: CameraParameters
)

@Serializable
data class CameraParameters(
    val sensor: Sensor,
    val focalLength: Double
)

@Serializable
data class Sensor(
    val width: Double,
    val height: Double
)

@Serializable
data class CreatorPosition(
    val geoPoint: MyGeoPoint,
    var metresAboveGround: Double
)

@Serializable
data class Landmark(
    var geoPoint: MyGeoPoint,
    var pixelPosition: PixelPosition
)

@Serializable
data class FieldOfViewBorders(
    var azimuthLeft: Double,
    var azimuthRight: Double
)

@Serializable
data class MyGeoPoint(
    val lat: Double,
    val lng: Double,
    var elevation: Double
)

@Serializable
data class MyLatLng(
    val lat: Double,
    val lng: Double
)

@Serializable
data class PixelPosition(
    val x: Int,
    var y: Int
)