package io.mapshot.sharedCommon.common.jsonReader

import kotlinx.serialization.Serializable


@Serializable
data class PoiResponse(
    val elements: List<PointOfInterest>
)

@Serializable
data class PointOfInterest(
    val type: String? = null,
    val id: Long? = null,
    val lat: Double? = null,
    val lon: Double? = null,
    val tags: PointOfInterestTags? = null
)

@Serializable
data class PointOfInterestTags(
    // General tags
    val name: String? = null,
    val wikipedia: String? = null,
    val wikidata: String? = null,
    val website: String? = null,

    // Settlement tags
    val population: String? = null,
    val place: String? = null,

    // Mountain tags
    val ele: String? = null,
)