    fun readElevationDataFileContent(elevationDataFileContent: String): ElevationDataFileContent? {
        return try {
            val lines = elevationDataFileContent.split("\n")
            var maxViewDistance = 0.0
            var stepSizeDistance = 0.0
            var stepSizeAngles = 0.0
            var currentAzimuth = ""
            val elevationDataStreams = mutableMapOf<String, MutableList<MyGeoPoint>>()

            for (line in lines) {
                when {
                    line.startsWith("maxViewDistance") -> maxViewDistance = line.split(":").last().trim().toDouble()
                    line.startsWith("stepSize_distance") -> stepSizeDistance = line.split(":").last().trim().toDouble()
                    line.startsWith("stepSize_angles") -> stepSizeAngles = line.split(":").last().trim().toDouble()
                    line.startsWith("azimuth") -> {
                        currentAzimuth = line.split(":").last().trim()
                        elevationDataStreams[currentAzimuth] = mutableListOf()
                    }

                    else -> {
                        val parts = line.split(",")
                        if (parts.size == 3) {
                            val latitude = parts[0].toDouble()
                            val longitude = parts[1].toDouble()
                            val elevation = parts[2].toDouble()
                            elevationDataStreams[currentAzimuth]?.add(
                                MyGeoPoint(
                                    latitude,
                                    longitude,
                                    elevation
                                )
                            )
                        }
                    }
                }
            }

            val azimuthElevationPointsMapAsStreamDataList = elevationDataStreams.map { (azimuth, elevationPoints) ->
                ElevationDataStream(azimuth.toDouble(), elevationPoints)
            }

            return ElevationDataFileContent(maxViewDistance, stepSizeDistance, stepSizeAngles, azimuthElevationPointsMapAsStreamDataList)
        } catch (e: Exception) {
            e.printStackTrace()
            null
        }
    }

