package io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion

import androidx.compose.ui.graphics.ImageBitmap
import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import io.mapshot.sharedCommon.common.api.overpassApi.PoiType
import io.mapshot.sharedCommon.common.folderHandling.kmmFile.KmmFolder
import io.mapshot.sharedCommon.common.jsonReader.CreatorPosition
import io.mapshot.sharedCommon.common.jsonReader.ElevationDataFileContent
import io.mapshot.sharedCommon.common.jsonReader.InfoFileData
import io.mapshot.sharedCommon.common.jsonReader.LatLng
import io.mapshot.sharedCommon.common.jsonReader.PoiResponse
import io.mapshot.sharedCommon.common.jsonReader.PointOfInterest
import io.mapshot.sharedCommon.common.jsonReader.asLatLng
import io.mapshot.sharedCommon.common.orientation.GeoCalculations
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.utils.ElevationDataOptimizer
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.utils.MapToMaskConverterHelper
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.utils.VisiblePointsCalculator
import io.mapshot.sharedCommon.mapshotViewer.ImagePointFromCoordinates
import io.mapshot.sharedCommon.mapshotViewer.mapshotPropsExtractor.MapshotPropsExtractor
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.launch
import kotlinx.coroutines.withContext

enum class MapToMaskConverterLoaderViewModelErrorType {
    ERROR_INIT_ONGOING,
    ERROR_MAPSHOT_PROPS_EXTRACTOR,
    ERROR_INFO_FILE_DATA,
    ERROR_ELEVATION_DATA
}

class MapToMaskConversionViewModel(
    private val originalImageBitmap: ImageBitmap,
    private val elevationDataFileContent: ElevationDataFileContent,
    private val infoFileData: InfoFileData,
    private val settlements: PoiResponse,
    private val mountains: PoiResponse
) : ViewModel() {

    data class MapToMaskMarker(val id: String, val latLng: LatLng, val imagePixel: ImagePointFromCoordinates?)
    data class MapToMaskPoi(val poiType: PoiType, val imagePixel: ImagePointFromCoordinates?, val poi: PointOfInterest)

    private val _mapToMaskMarker: MutableStateFlow<MapToMaskMarker?> = MutableStateFlow(null)
    private val _mapToMaskHorizonLine: MutableStateFlow<List<ImagePointFromCoordinates?>> = MutableStateFlow(emptyList())
    private val _mapToMaskPoiPoints: MutableStateFlow<List<MapToMaskPoi>> = MutableStateFlow(emptyList())
    private val _mapToMaskConverterHelperError: MutableStateFlow<MapToMaskConverterLoaderViewModelErrorType?> = MutableStateFlow(null)
    val mapToMaskMarker: StateFlow<MapToMaskMarker?> = _mapToMaskMarker.asStateFlow()
    val mapToMaskHorizonLine: StateFlow<List<ImagePointFromCoordinates?>> = _mapToMaskHorizonLine.asStateFlow()
    val mapToMaskPoiPoints: StateFlow<List<MapToMaskPoi>> = _mapToMaskPoiPoints.asStateFlow()
    val mapToMaskConverterHelperError: StateFlow<MapToMaskConverterLoaderViewModelErrorType?> = _mapToMaskConverterHelperError.asStateFlow()

    private var mapToMaskConverter: MapToMaskConverterHelper? = null
    private var initOngoing = true

    init {
        viewModelScope.launch {
            withContext(Dispatchers.Default) {
                createMapToMaskConverterHelper()
            }
        }
    }

    private fun createMapToMaskConverterHelper() {
        MapshotPropsExtractor().extractMapShotProps(infoFileData, originalImageBitmap)?.let { mapshotProps ->
            mapshotProps.imgCreatorPosition.geoPoint.elevation += mapshotProps.landmarkVerticalOffsetToFitHorizonLine
            mapshotProps.landmarkVerticalAngle = GeoCalculations().getVerticalAngleFromCreatorToRealPointInWorld(
                mapshotProps.imgCreatorPosition,
                mapshotProps.landmarkGeoPoint
            )

            val optimizedElevationData = ElevationDataOptimizer().optimizeElevationDataForCloseScatterPoints(
                infoFileData.imgCreatorPosition.geoPoint,
                infoFileData.landmark.geoPoint,
                elevationDataFileContent,
                mapshotProps.landmarkVerticalOffsetToFitHorizonLine
            )

            val visiblePointsCalculator = VisiblePointsCalculator(
                infoFileData.imgCreatorPosition,
                optimizedElevationData
            )

            val mapToMaskConverterHelper = MapToMaskConverterHelper(visiblePointsCalculator, mapshotProps)
            calculateHorizonLinePoints(mapToMaskConverterHelper, visiblePointsCalculator)
            addPoiListToPoiPoints(PoiType.SETTLEMENT, settlements, mapshotProps.imgCreatorPosition.asLatLng(), mapToMaskConverterHelper)
            addPoiListToPoiPoints(PoiType.MOUNTAIN, mountains, mapshotProps.imgCreatorPosition.asLatLng(), mapToMaskConverterHelper)

            mapToMaskConverter = mapToMaskConverterHelper
        } ?: run {
            _mapToMaskConverterHelperError.value =
                MapToMaskConverterLoaderViewModelErrorType.ERROR_MAPSHOT_PROPS_EXTRACTOR
        }
        initOngoing = false
    }

    private fun addPoiListToPoiPoints(
        poiType: PoiType,
        poiResponse: PoiResponse,
        imgCreatorPosition: LatLng,
        mapToMaskConverterHelper: MapToMaskConverterHelper
    ) {
        val currentPoiPoints = _mapToMaskPoiPoints.value
        val newPoints = poiResponse.elements.mapNotNull { poiPoint ->
            poiPoint.lat?.let { lat ->
                poiPoint.lon?.let { lon ->
                    MapToMaskPoi(
                        poiType,
                        mapToMaskConverterHelper.latLngToImagePoint(LatLng(lat, lon)),
                        poiPoint
                    )
                }
            }
        }
        val combinedPoints = currentPoiPoints.plus(newPoints)

        _mapToMaskPoiPoints.value = sortPoiElements(combinedPoints, imgCreatorPosition)
    }

    private fun sortPoiElements(unsortedPoiResponse: List<MapToMaskPoi>, imgCreatorPosition: LatLng): List<MapToMaskPoi> {
        return unsortedPoiResponse.sortedBy { mapToMaskPoi ->
            mapToMaskPoi.poi.lat?.let { lat ->
                mapToMaskPoi.poi.lon?.let { lon ->
                    GeoCalculations().calculateDistanceHaversine(imgCreatorPosition, LatLng(lat, lon))
                }
            }
        }
    }

    private fun calculateHorizonLinePoints(
        mapToMaskConverter: MapToMaskConverterHelper,
        visiblePointsCalculator: VisiblePointsCalculator
    ) {
        val mapToMaskHorizonLine = visiblePointsCalculator.getHorizonLine().map { horizonLinePoint ->
            horizonLinePoint.pointVisibility?.let {
                mapToMaskConverter.geoPointToImagePoint(it.elevationGeoPoint)
            }
        }
        _mapToMaskHorizonLine.value = mapToMaskHorizonLine
    }

    fun onMapTap(latLng: LatLng) {
        if (mapToMaskConverter == null && initOngoing) {
            _mapToMaskConverterHelperError.value =
                MapToMaskConverterLoaderViewModelErrorType.ERROR_INIT_ONGOING
            return
        }
        val markerId = "marker ${latLng.latitude} ${latLng.longitude}"
        val imagePixel = mapToMaskConverter?.latLngToImagePoint(latLng)

        // use the mercatorXYProjectionCheck value here to directly see in the app if the projection is correct
        // otherwise detecting the error is difficult
        _mapToMaskMarker.value = MapToMaskMarker(markerId, latLng, imagePixel)
    }
}