package io.mapshot.sharedCommon.mapshotViewer.image.utils

import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.unit.Constraints
import io.mapshot.sharedCommon.common.jsonReader.PixelPosition
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmSize

class PixelCanvasConverter(
    private val boxConstraints: Constraints,
    private val imageDimension: KmmSize
) {
    fun pixelPointToCanvas(
        scale: Float,
        offset: Offset,
        pixelPosition: PixelPosition
    ): Offset {
        val canvasX = pixelPosition.x * boxConstraints.maxWidth / imageDimension.width
        val canvasY = pixelPosition.y * boxConstraints.maxHeight / imageDimension.height

        val extraWidth = (scale - 1) * boxConstraints.maxWidth
        val extraHeight = (scale - 1) * boxConstraints.maxHeight

        val transformedPoint = Offset(
            x = (canvasX * scale + offset.x - extraWidth / 2),
            y = (canvasY * scale + offset.y - extraHeight / 2)
        )
        return transformedPoint
    }

    fun canvasToPixelPoint(
        tapOffset: Offset,
        scale: Float,
        offset: Offset
    ): PixelPosition {
        val extraWidth = (scale - 1) * boxConstraints.maxWidth
        val extraHeight = (scale - 1) * boxConstraints.maxHeight

        // Translate the canvas coordinates to image coordinates
        val canvasX = ((tapOffset.x - offset.x + extraWidth / 2) / scale)
        val canvasY = ((tapOffset.y - offset.y + extraHeight / 2) / scale)

        // canvas to pixel
        val pixelPositionX = (canvasX * imageDimension.width / boxConstraints.maxWidth).toInt()
        val pixelPositionY = (canvasY * imageDimension.height / boxConstraints.maxHeight).toInt()

        return PixelPosition(pixelPositionX, pixelPositionY)
    }
}