package io.mapshot.sharedCommon.mapshotViewer

import io.mapshot.sharedCommon.common.jsonReader.PointOfInterest
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmPointF


data class ImagePointFromCoordinates(
    var poiType: String?,

    var currentlyVisibleInMask: Boolean?,
    var poiOutOfPhotoViewBounds: Boolean? = null,

    // downloaded data from overpassApiOnGlobalScope
    var poi: PointOfInterest?,
    var labelBackground: PoiLabelPosition?,

    // image point info
    val directImagePoint: KmmPointF,
    val lastVisibleImagePoint: KmmPointF?,

    // mask point info
    var directMaskPoint: KmmPointF?,
    var lastVisibleMaskPoint: KmmPointF?,
    var partOfBox: Boolean = false, // the poi lies within a Box defined by the currently visible map area

    // precalculated info
    var distance: Double?,
    val hiddenBehindMountain: Boolean?
)

data class PoiLabelPosition(
    val labelStartPoint: KmmPointF?,
    val labelEndPoint: KmmPointF?
)
