package io.mapshot.sharedCommon.common.jsonReader

import kotlinx.serialization.SerialName
import kotlinx.serialization.Serializable


@Serializable
data class ErrorResponse(
    val nodes: List<LoadNodeResponse>? = null,
    @SerialName("node_ids") val nodeIds: List<String>? = null,
    val message: String,
)

@Serializable
data class LoadListOfNodesResponses(
    val nodes: List<LoadNodeResponse>,
    val message: String,
)

@Serializable
data class LoadNodeResponse(
    @SerialName("id") val nodeId: String,
    val name: String?,
    @SerialName("elevation_data") var infoFileData: InfoFileData, // todo: this string should be named 'infoFileData'
    @SerialName("user_rating") val userRating: UserRating,
    @SerialName("number_of_reports") val numberOfReports: Int,
    @SerialName("image_size") val imageSize: Int,
    @SerialName("preview_size") val previewUrl: Int,
)

@Serializable
data class UserRating(
    @SerialName("thumbs_ups") val thumbsUps: Int,
    @SerialName("thumbs_downs") val thumbsDowns: Int,
    @SerialName("reported") val reported: Boolean
)

@Serializable
data class GetUserRating(
    @SerialName("message") val message: String,
    @SerialName("reported") val reported: Boolean,
    @SerialName("thumbs_down") val thumbsDowns: Int,
    @SerialName("thumbs_up") val thumbsUps: Int
)

@Serializable
data class ListOfPublicMarkers(
    val nodes: List<PublicMarker>,
)

@Serializable
data class PublicMarker(
    @SerialName("id") val nodeId: String,
    val coordinates: Coordinates,
)

@Serializable
data class Coordinates(
    val latitude: Double,
    val longitude: Double
)

@Serializable
data class NodeUpdateInfoDataResponse(
    @SerialName("node_id") val nodeId: String,
    val message: String,
    @SerialName("elevation_data") val infoFileData: InfoFileData
)


@Serializable
data class SaveNodeIdResponse(
    @SerialName("node_id") val nodeId: String,
    val message: String,
    @SerialName("preview_url") val previewUrl: String,
    @SerialName("img_url") val imgUrl: String
)


@Serializable
data class DeleteNodeIdResponse(
    @SerialName("node_ids") val nodeIds: List<String>,
    val message: String
)


@Serializable
data class ElevationDownloadResponse(
    val results: List<ElevationData>
)

@Serializable
data class ElevationDownloadResponseForFullScatter(
    val results: List<List<Double>>
)

@Serializable
data class ElevationData(
    val elevation: Double,
    val location: MyLatLng
)

fun Coordinates.asLatLng(): LatLng {
    return LatLng(this.latitude, this.longitude)
}