package io.mapshot.sharedCommon.mapshotViewer.mapshotPropsExtractor

import androidx.compose.ui.graphics.ImageBitmap
import com.ashampoo.kim.Kim
import com.ashampoo.kim.common.convertToPhotoMetadata
import io.mapshot.sharedCommon.common.folderHandling._nativeCode.ByteArrayFileHandler
import io.mapshot.sharedCommon.common.folderHandling.FileNames
import io.mapshot.sharedCommon.common.folderHandling.kmmFile.KmmFile
import io.mapshot.sharedCommon.common.folderHandling.kmmFile.KmmFolder
import io.mapshot.sharedCommon.common.jsonReader.InfoFileData
import io.mapshot.sharedCommon.common.jsonReader.PixelPosition
import io.mapshot.sharedCommon.common.jsonReader.asLatLng
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmPointF
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmSize
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmSizeF
import io.mapshot.sharedCommon.common.logging.ErrorLogNumber
import io.mapshot.sharedCommon.common.logging.Logging
import io.mapshot.sharedCommon.common.orientation.AngleCalculationsForImg
import io.mapshot.sharedCommon.common.orientation.GeoCalculations

class MapshotPropsExtractor {

    fun extractMapShotProps(infoFileData: InfoFileData, originalImageBitmap: ImageBitmap): MapShotProps? {

        val sensorKmmSizeF = getSensorKmmSizeF(infoFileData)
        val scalingFactor = getScalingFactor(originalImageBitmap.width, sensorKmmSizeF, originalImageBitmap.height)
        val bitmapSize = getBitmapSize(originalImageBitmap.width, originalImageBitmap.height)
        val imgCreatorPosition = infoFileData.imgCreatorPosition
        val landmarkGeoPoint = infoFileData.landmark.geoPoint
        val landmarkPixelPosition = getLandmarkPixelPosition(infoFileData)

        val leftRightFovBorderATTENTIONThisIsNotIn360Range = AngleCalculationsForImg().getAzimuthImgLeftRightBorderATTENTIONThisIsNotIn360Range(
            bitmapSize.width,
            scalingFactor.x,
            infoFileData.imgCreatorDeviceInfo.cameraParameters.focalLength,
            sensorKmmSizeF,
            imgCreatorPosition.geoPoint.asLatLng(),
            landmarkPixelPosition.x,
            landmarkGeoPoint.asLatLng()
        )


        return MapShotProps(
            timestamp = infoFileData.timestamp,
            sensorKmmSizeF = sensorKmmSizeF,
            focalLength = infoFileData.imgCreatorDeviceInfo.cameraParameters.focalLength,
            scalingFactor = scalingFactor,
            stretchingFactorDueToSecondLandmark = getStretchingFactorDueToSecondLandmark(infoFileData),
            bitmapSize = bitmapSize,
            compassYawValue = getCompassYawValue(infoFileData),
            imageRotation = infoFileData.imageRotation,
            landmarkVerticalOffsetToFitHorizonLine = infoFileData.landmarkVerticalOffsetToFitHorizonLine,
            imgCreatorPosition = imgCreatorPosition,
            landmarkGeoPoint = landmarkGeoPoint,
            landmarkAzimuth = getLandmarkAzimuth(infoFileData),
            landmarkVerticalAngle = getLandmarkVerticalAngle(infoFileData),
            landmarkPixelPosition = landmarkPixelPosition,
            leftRightFovBorderATTENTIONThisIsNotIn360Range = leftRightFovBorderATTENTIONThisIsNotIn360Range
        )
    }

    private fun getLandmarkPixelPosition(infoFileData: InfoFileData) = PixelPosition(
        infoFileData.landmark.pixelPosition.x,
        infoFileData.landmark.pixelPosition.y
    )

    private fun getLandmarkVerticalAngle(infoFileData: InfoFileData) = GeoCalculations().getVerticalAngleFromCreatorToRealPointInWorld(
        infoFileData.imgCreatorPosition,
        infoFileData.landmark.geoPoint
    )

    private fun getLandmarkAzimuth(infoFileData: InfoFileData) = GeoCalculations().getAzimuth(
        infoFileData.imgCreatorPosition.geoPoint.asLatLng(),
        infoFileData.landmark.geoPoint.asLatLng()
    )

    private fun getCompassYawValue(infoFileData: InfoFileData): Float? {
        val dateInMsToSuppressOldImages =
            1719828000000 // 1719828000000 in ms is "2024-07-01 12:00:00" as a date // remove this when all old images have been removed from server

        return if (infoFileData.timestamp > dateInMsToSuppressOldImages) {
            infoFileData.compassYawValue
        } else {
            null // filters the first 150 images, where the compassYawValue was the pitchValue
        }
    }

    private fun getBitmapSize(imageWidth: Int, imageHeight: Int) = KmmSize(imageWidth, imageHeight)

    private fun getStretchingFactorDueToSecondLandmark(infoFileData: InfoFileData) = if (infoFileData.stretchingFactorDueToSecondLandmark == 0.0f) {
        1.0f
    } else {
        infoFileData.stretchingFactorDueToSecondLandmark
    }

    private fun getScalingFactor(
        imageWidth: Int,
        sensorKmmSizeF: KmmSizeF,
        imageHeight: Int
    ) = KmmPointF(
        imageWidth / sensorKmmSizeF.width,
        imageHeight / sensorKmmSizeF.height
    )

    private fun getSensorKmmSizeF(infoFileData: InfoFileData) = KmmSizeF(
        infoFileData.imgCreatorDeviceInfo.cameraParameters.sensor.width.toFloat(),
        infoFileData.imgCreatorDeviceInfo.cameraParameters.sensor.height.toFloat()
    )
}