package io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.utils

import io.mapshot.sharedCommon.common.jsonReader.ElevationDataFileContent
import io.mapshot.sharedCommon.common.jsonReader.MyGeoPoint
import io.mapshot.sharedCommon.common.jsonReader.asLatLng
import io.mapshot.sharedCommon.common.orientation.GeoCalculations

class ElevationDataOptimizer {
    fun optimizeElevationDataForCloseScatterPoints(
        imgCreatorGeoPoint: MyGeoPoint,
        landmarkGeoPoint: MyGeoPoint,
        elevationData: ElevationDataFileContent,
        altitudeOffsetAtImgCreatorPosition: Double
    ): ElevationDataFileContent {
        val distanceToLandmark = GeoCalculations().calculateDistanceHaversine(imgCreatorGeoPoint.asLatLng(), landmarkGeoPoint.asLatLng())
        elevationData.elevationDataStreams.forEach { elevationDataStream ->
            elevationDataStream.geoPoints.indices.forEach { index ->
                if (index * elevationData.stepSizeDistance > distanceToLandmark) {
                    // jump to next stream
                    return@forEach
                }
                val distanceToImgCreatorPosition = index * elevationData.stepSizeDistance
                val altitudeOffset = calculateOffsetForPointBetweenLandmarkAndImgCreator(
                    altitudeOffsetAtImgCreatorPosition,
                    distanceToImgCreatorPosition,
                    distanceToLandmark
                )
                elevationDataStream.geoPoints[index].elevation += altitudeOffset
            }
        }
        return elevationData
    }

    private fun calculateOffsetForPointBetweenLandmarkAndImgCreator(
        altitudeOffsetAtImgCreatorPosition: Double,
        distanceDesiredPointToImgCreatorPosition: Double,
        distanceLandmarkToImgCreatorPosition: Double
    ): Double {
        return altitudeOffsetAtImgCreatorPosition * (1 - (distanceDesiredPointToImgCreatorPosition) / distanceLandmarkToImgCreatorPosition)
    }
}