package io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.utils

import io.mapshot.sharedCommon.common.KmmMath
import io.mapshot.sharedCommon.common.jsonReader.LatLng
import io.mapshot.sharedCommon.common.jsonReader.MyGeoPoint
import io.mapshot.sharedCommon.common.jsonReader.PixelPosition
import io.mapshot.sharedCommon.common.jsonReader.asLatLng
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmPointF
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmSize
import io.mapshot.sharedCommon.common.logging.DebugLogNumber
import io.mapshot.sharedCommon.common.logging.Logging
import io.mapshot.sharedCommon.common.orientation.AngleCalculations
import io.mapshot.sharedCommon.common.orientation.AngleCalculationsForImg
import io.mapshot.sharedCommon.common.orientation.GeoCalculations
import io.mapshot.sharedCommon.mapshotViewer.ImagePointFromCoordinates
import io.mapshot.sharedCommon.mapshotViewer.mapshotPropsExtractor.MapShotProps
import kotlin.math.cos
import kotlin.math.sin
import kotlin.math.tan

class MapToMaskConverterHelper(
    private val visiblePointsCalculator: VisiblePointsCalculator,
    private val mapShotProps: MapShotProps
) {

    private var landmarkVerticalAngle = mapShotProps.landmarkVerticalAngle
    private var horizonLineUserAdjustmentRotation = mapShotProps.imageRotation

    fun latLngToImagePoint(desiredPointToMapOnMask: LatLng): ImagePointFromCoordinates? {
        val interpolatedGeoPointFromScatter =
            visiblePointsCalculator.getInterpolatedGeoPointFromScatter(desiredPointToMapOnMask, mapShotProps.imgCreatorPosition)
        if (interpolatedGeoPointFromScatter == null) {
            Logging().logDebug(DebugLogNumber.DEBUG_LOG_126, "interpolatedGeoPointFromScatter is null")
            return null
        }
        return getImagePoint(interpolatedGeoPointFromScatter)
    }

    fun geoPointToImagePoint(desiredPointToMapOnMask: MyGeoPoint): ImagePointFromCoordinates {
        return getImagePoint(desiredPointToMapOnMask)
    }

    private fun getImagePoint(interpolatedGeoPointFromScatter: MyGeoPoint): ImagePointFromCoordinates {
        var imagePointDirect = getImagePointDirect(
            mapShotProps,
            interpolatedGeoPointFromScatter
        )

        val imagePointDirectRotated = rotateAroundLandmarkPixel(mapShotProps.landmarkPixelPosition, imagePointDirect)
        imagePointDirect = KmmPointF(
            imagePointDirect.x,
            imagePointDirectRotated.y
        ) // using now only the vertical change of the rotation, as it is easier to calculate in the maskToMapConverter

        var imagePointLastVisible = getImagePointLastVisible(
            interpolatedGeoPointFromScatter.asLatLng(),
            imagePointDirect
        )

        var hidden = false
        if (imagePointLastVisible != null) {
            val imagePointLastVisibleRotated = rotateAroundLandmarkPixel(mapShotProps.landmarkPixelPosition, imagePointLastVisible)
            imagePointLastVisible = KmmPointF(imagePointLastVisible.x, imagePointLastVisibleRotated.y)
            if (imagePointDirect.y > imagePointLastVisible.y) {
                hidden = true
            }
        }

        return ImagePointFromCoordinates(
            poiType = null,
            currentlyVisibleInMask = null,
            poi = null,
            labelBackground = null,
            directImagePoint = imagePointDirect,
            lastVisibleImagePoint = imagePointLastVisible,
            directMaskPoint = null,
            lastVisibleMaskPoint = null,
            distance = null,
            hiddenBehindMountain = hidden
        )
    }

    private fun rotateAroundLandmarkPixel(landmarkPixelPosition: PixelPosition, imagePointDirect: KmmPointF): KmmPointF {
        val angle = KmmMath().toRadians(horizonLineUserAdjustmentRotation)

        // Translate the landmark pixel position to the origin
        val translatedX = imagePointDirect.x - landmarkPixelPosition.x
        val translatedY = imagePointDirect.y - landmarkPixelPosition.y

        // Perform rotation using the rotation matrix
        val rotatedX = translatedX * cos(angle) - translatedY * sin(angle)
        val rotatedY = translatedX * sin(angle) + translatedY * cos(angle)

        // Translate the rotated point back to its original position
        val resultX = rotatedX + landmarkPixelPosition.x
        val resultY = rotatedY + landmarkPixelPosition.y

        return KmmPointF(resultX.toFloat(), resultY.toFloat())
    }


    private fun getImagePointLastVisible(desiredPointToMapOnMask: LatLng, imagePointDirect: KmmPointF): KmmPointF? {
        val visibilityPointsOfLastVisible = getLastVisibleBeforeDirectPoint(desiredPointToMapOnMask)
        if (visibilityPointsOfLastVisible == null) {
            Logging().logDebug(DebugLogNumber.DEBUG_LOG_80, "visibilityPointsOfLastVisible is null")
            return null
        }

        val imagePointsLastVisible = mutableListOf<KmmPointF>()
        for (point in visibilityPointsOfLastVisible) {
            val imagePointLastVisible = getImagePointDirect(
                mapShotProps,
                point.elevationGeoPoint
            )
            imagePointsLastVisible.add(imagePointLastVisible)
        }
        // interpolate verticalAngle based on azimuth
        val desiredPointAzimuth =
            GeoCalculations().getAzimuth(mapShotProps.imgCreatorPosition.geoPoint.asLatLng(), desiredPointToMapOnMask)
        val lastVisibleLeftAzimuth = GeoCalculations().getAzimuth(
            mapShotProps.imgCreatorPosition.geoPoint.asLatLng(),
            visibilityPointsOfLastVisible[0].elevationGeoPoint.asLatLng()
        )
        val lastVisibleRightAzimuth = GeoCalculations().getAzimuth(
            mapShotProps.imgCreatorPosition.geoPoint.asLatLng(),
            visibilityPointsOfLastVisible[1].elevationGeoPoint.asLatLng()
        )
        val azimuthDiffToLeft = desiredPointAzimuth - lastVisibleLeftAzimuth
        val verticalAngleDiff = visibilityPointsOfLastVisible[1].verticalAngle - visibilityPointsOfLastVisible[0].verticalAngle
        val verticalAngleInterpolated =
            visibilityPointsOfLastVisible[0].verticalAngle + azimuthDiffToLeft / (lastVisibleRightAzimuth - lastVisibleLeftAzimuth) * verticalAngleDiff

        val verticalAngleInterpolatedDiffToLandmark = verticalAngleInterpolated - landmarkVerticalAngle

        // this is necessary as the landmark is not in the center anymore. This line handles the landmark out of center case. Otherwise angleSum* would just be *diffToLandmark.
        val angleSumY = AngleCalculations().calculateAngleSumFor180Range(
            verticalAngleInterpolatedDiffToLandmark,
            AngleCalculationsForImg().getVerticalAngleDiffToImageCenter(
                mapShotProps.bitmapSize.height,
                mapShotProps.scalingFactor.y,
                mapShotProps.focalLength,
                mapShotProps.landmarkPixelPosition.y
            )
        )

        val yOnCameraSensor = mapShotProps.focalLength.toFloat() * tan(KmmMath().toRadians(angleSumY))

        val yInImage = yOnCameraSensor * mapShotProps.scalingFactor.y

        val yInImageCentered = -yInImage + mapShotProps.bitmapSize.height.toFloat() / 2 // negative sign because y-axis is inverted

        return KmmPointF(imagePointDirect.x, yInImageCentered.toFloat())
    }

    private fun getLastVisibleBeforeDirectPoint(desiredPointToMapOnMask: LatLng): MutableList<PointVisibility>? {
        val desiredPointAzimuth = GeoCalculations().getAzimuth(
            mapShotProps.imgCreatorPosition.geoPoint.asLatLng(),
            desiredPointToMapOnMask
        )
        val desiredPointDistance = GeoCalculations().calculateDistanceHaversine(
            mapShotProps.imgCreatorPosition.geoPoint.asLatLng(),
            desiredPointToMapOnMask
        )

        val pointsVisibilityOfAzimuthNeighbours = visiblePointsCalculator.getStreamVisibilityOfAzimuthNeighbours(desiredPointAzimuth)
        if (pointsVisibilityOfAzimuthNeighbours == null) {
            Logging().logDebug(DebugLogNumber.DEBUG_LOG_81, "pointsVisibilityOfAzimuthNeighbours is null")
            return null
        }

        val verticalAnglesOfPointsBeforeDesiredPoint = mutableListOf<PointVisibility>()
        for (neighbour in pointsVisibilityOfAzimuthNeighbours) {
            // all points with distance smaller than the desiredPointDistance
            val pointsBeforeDesiredPoint = neighbour.streamVisibility.filter { (it.distance) < desiredPointDistance }

            if (pointsBeforeDesiredPoint.isEmpty()) {
                Logging()
                    .logDebug(DebugLogNumber.DEBUG_LOG_96, "pointsBeforeDesiredPoint is empty")
                return null
            }
            val lastVisiblePointBeforeDesiredPoint = pointsBeforeDesiredPoint.filter { it.visible }.maxByOrNull { it.distance } ?: return null
            verticalAnglesOfPointsBeforeDesiredPoint.add(lastVisiblePointBeforeDesiredPoint)
        }

        return verticalAnglesOfPointsBeforeDesiredPoint
    }

    private fun getImagePointDirect(
        mapShotProps: MapShotProps,
        desiredPointToMapOnMask: MyGeoPoint
    ): KmmPointF {

        val yInImageCentered = calculateYInImage(
            mapShotProps,
            desiredPointToMapOnMask
        )


        val xInImageCentered = calculateXInImage(
            mapShotProps.imgCreatorPosition.geoPoint.asLatLng(),
            mapShotProps.landmarkPixelPosition,
            mapShotProps.landmarkAzimuth,
            mapShotProps.bitmapSize,
            mapShotProps.scalingFactor,
            mapShotProps.focalLength,
            desiredPointToMapOnMask.asLatLng()
        )

        val xInImageCenteredStretchedAroundLandmark =
            xInImageCenteredStretchedAroundLandmark(mapShotProps.landmarkPixelPosition, xInImageCentered, mapShotProps.stretchingFactorDueToSecondLandmark)

        return KmmPointF(xInImageCenteredStretchedAroundLandmark.toFloat(), yInImageCentered.toFloat())
    }

    private fun xInImageCenteredStretchedAroundLandmark(
        landmarkPixelPosition: PixelPosition,
        xInImageCentered: Double,
        stretchingFactorDueToSecondLandmark: Float
    ): Double {
        val diffToLandmark = xInImageCentered - landmarkPixelPosition.x
        return diffToLandmark * stretchingFactorDueToSecondLandmark + landmarkPixelPosition.x
    }

    private fun calculateYInImage(
        mapShotProps: MapShotProps,
        desiredPointToMapOnMask: MyGeoPoint
    ): Double {
        val desiredPointVerticalAngle = GeoCalculations().getVerticalAngleFromCreatorToRealPointInWorld(
            mapShotProps.imgCreatorPosition,
            desiredPointToMapOnMask
        )
        val verticalAngleDiffToLandmark = desiredPointVerticalAngle - landmarkVerticalAngle
        val verticalAngleDiffToImageCenter = AngleCalculationsForImg().getVerticalAngleDiffToImageCenter(
            mapShotProps.bitmapSize.height,
            mapShotProps.scalingFactor.y,
            mapShotProps.focalLength,
            mapShotProps.landmarkPixelPosition.y
        )

        // this is necessary as the landmark is not in the center anymore. This line handles the landmark out of center case. Otherwise angleSum* would just be *diffToLandmark.
        val angleSumY = AngleCalculations().calculateAngleSumFor180Range(
            verticalAngleDiffToLandmark,
            verticalAngleDiffToImageCenter
        )
        val yOnCameraSensor = mapShotProps.focalLength.toFloat() * tan(KmmMath().toRadians(angleSumY))
        val yInImage = yOnCameraSensor * mapShotProps.scalingFactor.y
        return -yInImage + mapShotProps.bitmapSize.height.toFloat() / 2 // set centered // negative sign because y-axis origin is top of the image
    }

    fun updateLandmarkVerticalAngle(landmarkVerticalAngle: Double) {
        this.landmarkVerticalAngle = landmarkVerticalAngle
    }

    fun updateImgCreatorGeoPointElevation(newElevation: Double) {
        mapShotProps.imgCreatorPosition.geoPoint.elevation = newElevation
    }

    fun updateHorizonLineUserAdjustmentRotation(imageRotation: Double) {
        this.horizonLineUserAdjustmentRotation = imageRotation
    }

    companion object {
        fun calculateXInImage(
            imgCreatorLatLng: LatLng,
            landmarkPixelPosition: PixelPosition,
            landmarkAzimuth: Double,
            bitmapSize: KmmSize,
            scalingFactor: KmmPointF,
            focalLength: Double,
            desiredLatLngToMapOnMask: LatLng
        ): Double {
            val desiredPointAzimuth = GeoCalculations().getAzimuth(
                imgCreatorLatLng,
                desiredLatLngToMapOnMask
            )
            val azimuthDiffToLandmark = AngleCalculations().calculateAngleDiffFor180Range(desiredPointAzimuth, landmarkAzimuth)
            val horizontalAngleDiffToImageCenter = AngleCalculationsForImg().getHorizontalAngleDiffToImageCenter(
                bitmapSize.width,
                scalingFactor.x,
                focalLength,
                landmarkPixelPosition.x
            )

            // this is necessary as the landmark is not in the center anymore. This line handles the landmark out of center case. Otherwise angleSum* would just be *diffToLandmark.
            val anglesSumX = AngleCalculations().calculateAngleSumFor180Range(
                azimuthDiffToLandmark,
                horizontalAngleDiffToImageCenter
            )

            val xOnCameraSensor = focalLength.toFloat() * tan(KmmMath().toRadians(anglesSumX))
            val xInImage = xOnCameraSensor * scalingFactor.x
            return xInImage + bitmapSize.width.toFloat() / 2 // set centered
        }
    }
}