package io.mapshot.sharedCommon.mapshotViewer.image

import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.unit.Constraints
import androidx.lifecycle.ViewModel
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow

class ZoomStateViewModel() : ViewModel() {
    private val _scale: MutableStateFlow<Float> = MutableStateFlow(1f)
    private val _translation: MutableStateFlow<Offset> = MutableStateFlow(Offset.Zero)
    val scale: StateFlow<Float> = _scale.asStateFlow()
    val translation: StateFlow<Offset> = _translation.asStateFlow()

    fun updateZoomState(zoomChange: Float, pan: Offset, constraints: Constraints) {
        val previousScale = _scale.value
        val newScale = (previousScale * zoomChange).coerceIn(1f, 100f)
        _scale.value = newScale

        // Calculate extra width/height due to the new scale
        val extraWidth = (newScale - 1) * constraints.maxWidth
        val extraHeight = (newScale - 1) * constraints.maxHeight

        val maxX = extraWidth / 2
        val maxY = extraHeight / 2

        val scaleRatio = newScale / previousScale
        val adjustedTranslation = Offset(
            x = (_translation.value.x * scaleRatio + pan.x).coerceIn(-maxX, maxX),
            y = (_translation.value.y * scaleRatio + pan.y).coerceIn(-maxY, maxY)
        )

        _translation.value = adjustedTranslation
    }

}