data class AllDataNecessaryForMapshotViewer(
    val originalImageBitmap: ImageBitmap,
    val elevationDataFileContent: ElevationDataFileContent,
    val infoFileData: InfoFileData,
    val settlements: PoiResponse,
    val mountains: PoiResponse
)


@Composable
fun MapshotViewer(nodeFolder: KmmFolder) {

    val mapshotViewerDataLoaderViewModel = viewModel { MapshotViewerDataLoaderViewModel() }
    mapshotViewerDataLoaderViewModel.loadAllDataNecessaryForMapshotViewer(nodeFolder)

    val allDataNecessaryForMapshotViewer = mapshotViewerDataLoaderViewModel.allDataNecessaryForMapshotViewer.collectAsState()

    allDataNecessaryForMapshotViewer.value?.let { dataForMapshotViewer ->
        val mapToMaskConversionViewModel: MapToMaskConversionViewModel =
            viewModel { MapToMaskConversionViewModel(
                dataForMapshotViewer.originalImageBitmap,
                dataForMapshotViewer.elevationDataFileContent,
                dataForMapshotViewer.infoFileData,
                dataForMapshotViewer.settlements,
                dataForMapshotViewer.mountains
            ) }

        Column(
            modifier = Modifier.fillMaxSize()
        ) {
            Box(
                modifier = Modifier
                    .fillMaxWidth()
                    .fillMaxHeight(0.5f),
                contentAlignment = Alignment.Center
            )
            {
                MapshotViewerImage(
                    dataForMapshotViewer.originalImageBitmap,
                    mapToMaskConversionViewModel
                )
            }
            Box(
                modifier = Modifier
                    .fillMaxWidth()
                    .fillMaxHeight()
                    .background(color = MaterialTheme.colors.background),
                contentAlignment = Alignment.Center
            )
            {
                MapshotViewerMap(mapToMaskConversionViewModel)
            }
        }
    }

}
