package io.mapshot.sharedCommon.mapshotViewer

import androidx.compose.runtime.Composable
import androidx.lifecycle.ViewModel
import io.mapshot.sharedCommon.common.folderHandling.FileNames
import io.mapshot.sharedCommon.common.folderHandling._nativeCode.getImageBitmap
import io.mapshot.sharedCommon.common.folderHandling._nativeCode.loadTextFromFile
import io.mapshot.sharedCommon.common.folderHandling.kmmFile.KmmFile
import io.mapshot.sharedCommon.common.folderHandling.kmmFile.KmmFolder
import io.mapshot.sharedCommon.common.jsonReader.JsonReader
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.asStateFlow

class MapshotViewerDataLoaderViewModel: ViewModel() {
    private var _allDataNecessaryForMapshotViewer: MutableStateFlow<AllDataNecessaryForMapshotViewer?> = MutableStateFlow(null)
    val allDataNecessaryForMapshotViewer: StateFlow<AllDataNecessaryForMapshotViewer?> = _allDataNecessaryForMapshotViewer.asStateFlow()

    @Composable
    fun loadAllDataNecessaryForMapshotViewer(nodeFolder: KmmFolder) {
        val originalImageBitmap = getImageBitmap(KmmFile(nodeFolder, FileNames.IMAGE_FILE))
        val elevationDataFileContent = loadTextFromFile(KmmFile(nodeFolder, FileNames.ELEVATION_DATA_FILE))?.let { elevationDataFileContentAsString ->
            JsonReader().readElevationDataFileContent(elevationDataFileContentAsString)
        }
        val infoFileData = loadTextFromFile(KmmFile(nodeFolder, FileNames.MAPSHOT_INFO_FILE))?.let { infoFileDataAsString ->
            JsonReader().readLoadListOfNodesResponse(infoFileDataAsString)?.nodes?.get(0)?.infoFileData
        }
        val settlementsFile = KmmFile(nodeFolder, FileNames.FILENAME_FOR_SETTLEMENTS_OVERPASS_DATA)
        val settlements = loadTextFromFile(settlementsFile)?.let { settlementsAsString ->
            JsonReader().readOverpassApiPoiResponseData(settlementsAsString)
        }
        val mountainsFile = KmmFile(nodeFolder, FileNames.FILENAME_FOR_MOUNTAINS_OVERPASS_DATA)
        val mountains = loadTextFromFile(mountainsFile)?.let { mountainsAsString ->
            JsonReader().readOverpassApiPoiResponseData(mountainsAsString)
        }

        originalImageBitmap?.let {
            elevationDataFileContent?.let {
                infoFileData?.let {
                    settlements?.let {
                        mountains?.let {
                            _allDataNecessaryForMapshotViewer.value = AllDataNecessaryForMapshotViewer(
                                originalImageBitmap,
                                elevationDataFileContent,
                                infoFileData,
                                settlements,
                                mountains
                            )
                        }
                    }
                }
            }
        }
    }
}