package io.mapshot.sharedCommon.common.api

import io.ktor.client.request.get
import io.ktor.client.statement.HttpResponse
import io.mapshot.sharedCommon.common.api._nativeCode.createHttpClient

class GithubPagesApi {
    private val client = createHttpClient()

    suspend fun getElevationDataFileContent(): HttpResponse {
        return client.get("https://sischinger.github.io/githubPage/data/elevationData.txt")
    }

    suspend fun getSettlementsDataFileContent(): HttpResponse {
        return client.get("https://sischinger.github.io/githubPage/data/settlementsOverpass.txt")
    }

    suspend fun getMountainsDataFileContent(): HttpResponse {
        return client.get("https://sischinger.github.io/githubPage/data/mountainsOverpass.txt")
    }

}