package io.mapshot.sharedCommon.mapshotViewer.image.mask

import androidx.compose.foundation.Canvas
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material.LocalTextStyle
import androidx.compose.runtime.Composable
import androidx.compose.runtime.State
import androidx.compose.ui.Modifier
import androidx.compose.ui.geometry.CornerRadius
import androidx.compose.ui.geometry.Offset
import androidx.compose.ui.geometry.Size
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.drawscope.DrawScope
import androidx.compose.ui.graphics.drawscope.rotate
import androidx.compose.ui.text.TextLayoutResult
import androidx.compose.ui.text.drawText
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.text.rememberTextMeasurer
import androidx.compose.ui.text.style.TextOverflow
import androidx.compose.ui.unit.dp
import io.mapshot.sharedCommon.common.jsonReader.PixelPosition
import io.mapshot.sharedCommon.common.kmmDataClasses.KmmPointF
import io.mapshot.sharedCommon.mapshotViewer.image.utils.PixelCanvasConverter
import io.mapshot.sharedCommon.mapshotViewer.mapToMaskConversion.MapToMaskConversionViewModel


@Composable
fun PoiLabel(
    mountain: MapToMaskConversionViewModel.MapToMaskPoi,
    pixelCanvasConverter: PixelCanvasConverter,
    scale: State<Float>,
    translation: State<Offset>,
    directImagePoint: KmmPointF,
    backgroundColor: Color,
    onBackgroundColor: Color
) {
    val textMeasurer = rememberTextMeasurer(cacheSize = 0)
    val materialTextStyle = LocalTextStyle.current.copy(
        fontWeight = FontWeight.Bold,
    )
    Canvas(modifier = Modifier.fillMaxSize()) {
        mountain.poi.tags?.name?.let { mountainName ->
            val anchorPoint =
                pixelCanvasConverter.pixelPointToCanvas(scale.value, translation.value, PixelPosition(directImagePoint.x.toInt(), directImagePoint.y.toInt()))
            val textLayoutResult = textMeasurer
                .measure(
                    text = mountainName,
                    style = materialTextStyle,
                    overflow = TextOverflow.Ellipsis,
                )

            rotate(degrees = 270f, pivot = anchorPoint) {
                val textHeight = textLayoutResult.size.height
                val distanceToAnchorPoint = 20.dp.toPx()
                val newAnchorPointForCenteredLabel = Offset(anchorPoint.x + distanceToAnchorPoint, anchorPoint.y - textHeight / 2)
                textBackground(newAnchorPointForCenteredLabel, textLayoutResult, backgroundColor)
                textOverBackground(newAnchorPointForCenteredLabel, textLayoutResult, onBackgroundColor)
            }
            drawCircle(
                color = Color.Black,
                center = anchorPoint,
                radius = 10f
            )
        }
    }
}


private fun DrawScope.textOverBackground(
    anchorPoint: Offset,
    textLayoutResult: TextLayoutResult,
    onBackgroundColor: Color
) {
    drawText(
        textLayoutResult,
        color = onBackgroundColor,
        topLeft = anchorPoint
    )
}

private fun DrawScope.textBackground(
    anchorPoint: Offset,
    textLayoutResult: TextLayoutResult,
    backgroundColor: Color
) {
    val textWidth = textLayoutResult.size.width
    val textHeight = textLayoutResult.size.height
    val paddingWidth = textHeight * 0.3f
    val paddingHeight = textHeight * 0.1f
    val fullWidth = textWidth + 2 * paddingWidth
    val fullHeight = textHeight + 2 * paddingHeight
    val cornerRadius = CornerRadius(fullHeight / 2, fullHeight / 2)
    shadowRect(anchorPoint, paddingWidth, paddingHeight, textWidth, textHeight, cornerRadius)
    coloredBackground(anchorPoint, paddingWidth, paddingHeight, fullWidth, fullHeight, cornerRadius, backgroundColor)
}

private fun DrawScope.shadowRect(
    anchorPoint: Offset,
    paddingWidth: Float,
    paddingHeight: Float,
    textWidth: Int,
    textHeight: Int,
    cornerRadius: CornerRadius
) {
    val shadowColor = Color.Black.copy(alpha = 0.5f)
    val shadowOffset = Offset(-10f, 10f)

    drawRoundRect(
        color = shadowColor,
        topLeft = anchorPoint.copy(
            x = anchorPoint.x - paddingWidth + shadowOffset.x,
            y = anchorPoint.y - paddingHeight + shadowOffset.y
        ),
        size = Size(
            textWidth + 2 * paddingWidth,
            textHeight + 2 * paddingHeight
        ),
        cornerRadius = cornerRadius
    )
}

private fun DrawScope.coloredBackground(
    anchorPoint: Offset,
    paddingWidth: Float,
    paddingHeight: Float,
    fullWidth: Float,
    fullHeight: Float,
    cornerRadius: CornerRadius,
    backgroundColor: Color
) {
    drawRoundRect(
        color = backgroundColor,
        topLeft = anchorPoint.copy(
            x = anchorPoint.x - paddingWidth,
            y = anchorPoint.y - paddingHeight
        ),
        size = Size(fullWidth, fullHeight),
        cornerRadius = cornerRadius
    )
}
