package io.mapshot.sharedCommon.common.orientation

class AngleCalculations {

    fun calculateAngleDiffFor180Range(angle1: Double, angle2: Double): Double {
        val difference = angle1 - angle2
        return when {
            difference > 180 -> difference - 360
            difference < -180 -> difference + 360
            else -> difference
        }
    }

    fun calculateAngleSumFor180Range(angle1: Double, angle2: Double): Double {
        val sum = angle2 + angle1
        return when {
            sum > 180 -> sum - 360
            sum < -180 -> sum + 360
            else -> sum
        }
    }
}